[data-theme='1'] {
  --color: #000000;
  --background: #ffffff;
}

[data-theme='2'] {
  --color: #000000;
  --background: #ddeeff;
}

[data-theme='3'] {
  --color: #ffffff;
  --background: #212a34;
}

html {
  background-color: var(--background);
  color: var(--color);
}

.select {
  width: 170px;
  height: 35px;
}
